import "./styles.css";

// initial todos
// DO NOT EDIT THIS ARRAY
// You may add props to objects if needed.
let todos = [
  {
    todoID: 0,
    todoText: "Finish Homework",
    todoComplete: false
  },
  {
    todoID: 1,
    todoText: "Walk the dog",
    todoComplete: true
  },
  {
    todoID: 2,
    todoText: "Clean my room",
    todoComplete: false
  }
];

function displayTodos() {
  let todosList = "";
  for (let i = 0; i < todos.length; i++) {
    todosList += "<li>" + todos[i].todoText + "</li>";
  }
  document.querySelector(".todoList").innerHTML = todosList;
}
displayTodos(todos);
